@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@700&family=Titan+One&display=swap";
body {
  font-family: var(--primary-font);
  letter-spacing: .04em;
  color: var(--primary-text-color);
}

h1, h2, h3, ul, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.container {
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 34px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 115px;
  }
}

:root {
  --primary-font: "DM Sans", sans-serif;
  --secondary-font: "Titan One", cursive, sans-serif;
  --primary-text-color: #fff;
  --secondary-text-color: #000;
  --accent-color: #d41443;
  --info-color: #ffa5ba;
  --description-color: #907e82;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button {
  letter-spacing: .06em;
  color: var(--accent-color);
  background-color: var(--primary-text-color);
  cursor: pointer;
  border-style: none;
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.button:hover, .button:focus {
  background-color: #ecebeb;
}

.button--red {
  background-color: var(--accent-color);
  color: var(--primary-text-color);
}

.button--red:hover, .button--red:focus {
  background-color: #c7133e;
}

.title {
  font-family: var(--secondary-font);
  letter-spacing: .06em;
  text-transform: uppercase;
  color: var(--accent-color);
  font-size: 58px;
  line-height: 1.14;
}

.subtitle {
  color: var(--secondary-text-color);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.86;
}

.container {
  width: 1280px;
  height: 664px;
}

